.main,
.container,
.sidebar,
.inner,
.application {
  position: relative;
  height: 100%; }

.main {
  overflow: auto; }
.inner {
  min-width: 980px;
  @include clr; }

.content {
  position: relative;
  z-index: 5; }

.container {
  position: relative;
  &_right {
    float: right;
    width: 60%; }
  &_upper {
    z-index: 15;
    background: #f8f8f8;
    box-shadow: 3px 0 4px 5px rgba(0, 0, 0, 0.2); }
  &_overlay {
    position: absolute;
    z-index: 5;
    top: 0;
    right: 0;
    bottom: 0;
    width: 60%;
    box-shadow: -3px 0 4px 5px rgba(0, 0, 0, 0.2);
    transform: translate3d(-110%,0,0);
    transition: transform 0.3s ease-in-out;
    &.is-open {
      @include gpu; } }
  &__body {
    position: relative;
    height: calc(100% - 50px);
    // position: absolute
    // top: 50px
    // left: 0
    // right: 0
 } }    // bottom: 0

.sidebar {
  position: relative;
  z-index: 10;
  display: flex;
  flex-direction: column;
  background: $alabaster;
  &_left {
    // z-index: 1
    float: left;
    width: 40%;
    box-shadow: -3px 0 4px 5px rgba(0, 0, 0, 0.2); }
  &_white {
    background: $white; }
  &__body {
    position: relative;
    overflow: auto;
    flex: 1; }
  &__toolbar {
    .toolbar {
      &__col {
        &:not(:last-child) {
          border-color: #89cb8c; }
        &:first-child {
          @include refund {
            width: 0;
            opacity: 0;
            visibility: hidden;
            pointer-events: none; } } }
      &__button {
        height: 48px;
        line-height: 48px;
        @include refund {
          background: $red; } } } }
  //state
  &.is-disabled {
    .sidebar {
      &__toolbar,
      &__menu {
        display: none; } }
    .header__delete {
      display: none; } } }

.items {
  &__top {
    height: 180px; }
  &__body {
    height: calc(100% - 180px);
    // position: absolute
    // top: 180px
    // left: 0
    // right: 0
 } }    // bottom: 0


.wrapper {
  margin: 10px 0; }

.section {
  padding: 20px 0;
  &_inner {
    padding: 20px; } }

.settings {
  &__sidebar,
  &__container {
    position: relative;
    height: 100%; }
  &__sidebar {
    float: left;
    width: 300px; }
  &__container {
    margin-left: 300px;
    box-shadow: 3px 0 4px 5px rgba(0, 0, 0, 0.2); }
  .settings__header {
    position: absolute;
    top: 0;
    left: 0;
    right: 0; }
  &__nav {
    overflow: auto;
    padding-top: 70px;
    height: 100%;
    background: #434c55; }
  &__body {
    display: flex;
    flex-direction: column;
    padding-top: 50px;
    height: 100%;
    &_default {
      display: block;
      padding: 80px 20px 30px; } }
  &__explanation,
  &__footer {
    padding: 20px; }
  &__content {
    position: relative;
    height: calc(100% - 200px); } }

.table-wrapper {
  position: relative;
  // overflow: auto
  overflow: hidden;
  padding: 37px 0 61px;
  height: 100%;
  &_no-footer {
    padding-bottom: 0; }
  &_header-lg {
    padding-top: 50px;
    .table-wrapper {
      &__header {
        height: 50px; } } }
  &__header {
    position: absolute;
    z-index: 2;
    top: 0;
    left: 0;
    right: 0;
    height: 37px;
    &:before {
      content: '';
      position: absolute;
      top: 0;
      left: 100%;
      bottom: -1px;
      border-bottom: 1px solid #aaa;
      width: 20px;
      background: #eee;
      margin-left: -6px; } }
  &__footer {
    position: relative;
    z-index: 2;
    margin-bottom: -61px;
    height: 61px;
    background: $alabaster; }
  &__body {
    overflow: auto;
    // margin-bottom: -61px
    max-height: 100%;
    // &:after
    //   content: ''
    //   display: block
    //   clear: both
 }    //   height: 61px
  // &__header,
  // &__body,
  // &__footer
  &__value,
  &__meta {
    word-wrap: break-word; }
  &__meta {
    opacity: 0.5; }

  //min sizes
  &[data-table="terminal"] {
    [data-table-fake],
    [data-table-scrollable] > div {
      min-width: 980px; } }
  &[data-table="orders"] {
    [data-table-fake],
    [data-table-scrollable] > div {
      min-width: 1200px; } } }

.table-header,
.table-body {
  display: table;
  table-layout: fixed;
  width: 100%;
  font-size: 14px;
  &__tr {
    display: table-row; }
  &__th,
  &__td {
    display: table-cell;
    vertical-align: middle;
    padding: 10px;
    &:first-child {
      padding-left: 20px; }
    &[data-column="icon"] {
      width: 60px; }
    &[data-column="orders-status"] {
      width: 70px; }
    &[data-column="orders-action"] {
      width: 110px;
      text-align: right; }
    &[data-column="orders-id"] {
      width: 19%; }
    &[data-column="orders-register"],
    &[data-column="orders-type"],
    &[data-column="orders-fulfillment"],
    &[data-column="orders-date"] {
      width: 8%; }
    &[data-column="orders-store"],
    &[data-column="orders-employee"] {
      width: 10%; } }
  &__th {
    @include rmed; } }

.table-header {
  border-bottom: 1px solid $silverGray;
  background: $lightGray; }

.table-body {
  &_orders {
    .table-body {
      &__tr {
        &:nth-child(even) {
          background: $white;
          transition: background 0.1s ease-in-out; }
        @include hover {
          background: $azure;
          cursor: pointer; }
        @include active {
          background: $azureActive; } }
      &__td {
        padding-top: 5px;
        padding-bottom: 5px; } } }
  &_order {
    .table-body {
      &__tr {
        &:nth-child(even) {
          background: $white; } } } }
  &__tr {
    &:not(:first-child) {
      .table-body {
        &__td {
          border-top: 1px solid $lightGray; } } } } }

.table-footer {
  padding: 10px 20px;
  border-top: 1px solid $lightGray; }

