.header__icons {
  float: right;
  margin: 0;
  font-size: 0;
  .icon {
    vertical-align: top;
    margin: 5px 5px 0 0; } }

article {
  position: absolute;
  height: 100%;
  width: 100%;
  background: #f8f8f8;
  main {
    position: absolute;
    height: 100%;
    width: 100%;
    &.right {
      right: 0;
      width: 60%; } }
  aside {
    position: absolute;
    right: 0;
    height: 100%;
    &.left {
      right: auto;
      left: 0;
      width: 40%; } } }

.layout {
  display: flex;
  flex-flow: row;

  &-rows {
    flex-flow: column; }

  &-columns {
    flex-flow: row; }

  &__column {
    flex: 1; }

  &__column-grow {
    flex: 10; }

  &__column-gap {
    width: 20px;
    flex: 0 0 20px; }

  &__row {
    flex: content; }

  &__row-grow {
    flex: 2;
    overflow-y: auto; } }
